@import-normalize; /* bring in normalize.css styles */

/*App styles */
body {
  font-family: ArsMaquette-Black, "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: 500;
  font-size: 14px;
  color: #3c444d;
  background-color: #fff;
  letter-spacing: -0.02em;
  box-sizing: border-box;
}

/* Hero styles */
#section-hero {
  background-image: url(./assets/img/home/newhp-header.jpg);
  background-position: center;
  background-size: cover;
  height: auto;
  height: 90vh;
  text-align: center;
}

#section-hero h1 {
  font-size: 1.6rem;
  font-weight: bold;
  color: #007bff;
  line-height: 1.14em;
  margin-bottom: 0;
  padding: 0;
}

#section-hero p {
  font-size: 1.2rem;
  font-weight: 300;
  color: #464646;
  margin-bottom: 0;
}

.hero-text {
  padding: 10px 0;
  background: hsla(0, 0%, 100%, 0.85);
}

/* Blokes styles */
#section-blokes {
  display: flex;
  align-items: center;
  flex-direction: row;
  width: 100%;
  text-align: center;
  background-color: #f0f2f2;
}

#section-blokes .section-content {
  flex-direction: column;
  width: 50%;
}

#section-blokes .section-text {
  width: 60%;
  margin: 2rem auto auto;
}

#section-blokes .title {
  font-size: 24pt;
  font-weight: lighter;
  text-align: center;
  color: #003b70;
  margin-bottom: 1rem;
}

#section-blokes .section-button {
  font-size: 14pt;
  font-weight: 900;
  color: #fff;
  background-color: #003b70;
  text-align: center;
  max-width: 225px;
  padding: 1rem;
  margin: auto;
}

#section-blokes .section-image {
  width: 50%;
}

#section-blokes .section-image img {
  width: 100%;
  vertical-align: middle;
  border-style: none;
}

/* WhyItWorks styles */
#section-why {
  text-align: center;
}
#section-why .section-header {
  background-image: url(./assets/img/home/new-why-12wbt-will-work.jpg);
  background-size: cover;
  background-position: top;
  min-height: 350px;
  align-content: center;
}

#section-why .section-header .section-title {
  font-size: 2em;
  font-weight: bold;
  letter-spacing: 0.01rem;
  color: #fff;
  margin: auto;
}

#section-why .section-content {
  background-color: #f4f4f4;
  padding: 2em 0;
}

#section-why .section-content h3 {
  font-family: ArsMaquette-Black, "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 1.9rem;
  font-weight: 900;
  letter-spacing: 0.01rem;
  line-height: 2.1rem;
  color: #003b70;
}

#section-why .section-content p {
  font-size: 1.2rem;
  font-weight: 300;
  line-height: 1.6em;
  margin: 0 0 1.2em;
}

/* FitnessPrograms styles */
#fitness-programs {
  padding: 1rem 0;
}
#fitness-programs .section-title {
  font-size: 1.9rem;
  line-height: 2.1rem;
  color: #003b70;
  text-align: center;
  margin: 0.8em 0;
}

#fitness-programs .fitness-programs-cta {
  padding: 10px;
  background-color: #f4f4f4;
  box-shadow: 8px 8px 5px 0 rgba(85, 85, 85, 0.15);
}

#fitness-programs .fitness-programs-cta h3 {
  font-size: 1.6rem;
  font-weight: bold;
  font-family: ArsMaquette, "Helvetica Neue", Helvetica, Arial, sans-serif;
  text-align: center;
  line-height: 2.1rem;
  color: #007ce0;
  margin: 0.8em 0;
}

#fitness-programs .fitness-programs-cta .btn {
  font-size: 1.2rem;
  font-weight: 600;
  text-transform: uppercase;
  text-shadow: none;
  color: #fff;
  background: #00b5f2;
  border: none;
  border-radius: 0;
  padding: 10px;
  width: 100%;
}

#fitness-programs .fitness-programs-cta p {
  font-size: 0.9rem;
  font-weight: 300;
  text-align: center;
  color: #3c444d;
  margin: 0.4rem 0;
}

#fitness-programs .card-collection {
  display: flex;
  flex-wrap: wrap;
  margin: 15px 0;
}

#fitness-programs .card {
  margin-bottom: 15px;
  display: block;
  height: 100%;
  position: relative;
  padding-top: 180px;
  background-size: cover;
  background-position: top;
  border: none;
  border-radius: 0;
}

#fitness-programs .card .card-title {
  text-align: center;
  position: absolute;
  bottom: 15px;
  background: #fff;
  width: 90%;
  margin: 0 5%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  height: 54px;
  padding: 5px;
  font-family: ArsMaquette-Black, "Helvetica Neue", Helvetica, Arial, sans-serif;
  letter-spacing: 0.01rem;
  font-weight: 900;
  font-size: 1.3rem;
  text-transform: uppercase;
  color: #007de0;
  z-index: 2;
  border: 0;
  font-style: normal;
  line-height: 1;
  text-decoration: none;
}

.weight-loss {
  background-image: url(./assets/img/home/card/weight-loss.jpg);
}

.strength {
  background-image: url(./assets/img/home/card/strength.jpg);
}

.running {
  background-image: url(./assets/img/home/card/running.jpg);
}

.post-baby {
  background-image: url(./assets/img/home/card/post-baby.jpg);
}

.gray-overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  background-color: rgba(51, 51, 51, 0.5);
}

/* Benefits */
.benefits-title h2 {
  font-size: 2.2rem;
  font-weight: 900;
  font-family: ArsMaquette-Black, "Helvetica Neue", Helvetica, Arial, sans-serif;
  letter-spacing: 0.01rem;
  line-height: 2.1rem;
  text-align: center;
  color: #003b70;
  margin: 0.2em 0;
}

.benefits-list h3 {
  font-size: 1.7rem;
  font-family: ArsMaquette, "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: 700;
  text-align: center;
  text-transform: none;
  line-height: 2rem;
  color: #fff;
  box-shadow: 8px 8px 5px 0 rgba(85, 85, 85, 0.15);
  padding: 6.67px;
  margin: 0.6em 0;
}

.benefits-list ul {
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  text-align: left;
  text-decoration: none;
  line-height: 1;
  list-style: none;
  margin: 0;
  padding: 0;
  border: 0;
}

.benefits-list li {
  font-size: 16px;
  font-weight: 300;
  text-align: center;
  line-height: 1.6em;
  color: #3c444d;
  margin: 5px 0;
  padding-left: 0;
}

.benefits-list span {
  position: relative;
  margin-left: 30px;
  font-size: 1.2rem;
}

span::before {
  position: absolute;
  content: "";
  width: 12px;
  height: 12px;
  top: 6px;
  left: -24px;
  border-radius: 10px;
}

.nutrition h3,
.nutrition span::before {
  background-color: #3de5c7;
}

.fitness h3,
.fitness span::before {
  background-color: #ee3642;
}

.mindset h3,
.mindset span::before {
  background-color: #f573b0;
}

.support h3,
.support span::before {
  background-color: #634cb7;
}

.free-sample {
  text-align: center;
}

.free-sample a,
.free-sample a:hover {
  display: block;
  font-size: 1.2rem;
  line-height: 1.5rem;
  text-decoration: none;
  color: #fff;
  background: #01c460;
  white-space: normal;
  box-shadow: none;
  text-shadow: none;
  margin: 20px 0;
  width: 100%;
  padding: 15px;
  border-radius: 0;
}

/* SocialBanner styles */
#section-social {
  position: relative;
  width: 100%;
  text-align: center;
  background-color: #f4f4f4;
  padding: 30px 0;
  overflow: hidden;
}

#section-social h2 {
  font-size: 1.9rem;
  font-weight: 900;
  font-family: ArsMaquette, "Helvetica Neue", Helvetica, Arial, sans-serif;
  line-height: 2.1rem;
  text-transform: none;
  text-shadow: none;
  color: #003b70;
}

#section-social a,
#section-social a:hover {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: 700;
  text-decoration: none;
}

#section-social img {
  width: 2rem;
  height: 2rem;
}

.social-name {
  display: block;
  margin: 0.5em auto;
  text-align: center;
  color: #999;
}

/* CallToAction */
#section-cta {
  position: relative;
  width: 100%;
  background: url(./assets/img/home/1millionkilosgroup-footer.jpg) 50% / cover
    no-repeat;
  padding: 60px 0;
  margin-bottom: 50px;
  overflow: hidden;
}

#section-cta .gray-overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  background-color: rgba(51, 51, 51, 0.5);
}

#section-cta .section-title {
  font-size: 1.9rem;
  font-weight: bold;
  font-family: ArsMaquette, "Helvetica Neue", Helvetica, Arial, sans-serif;
  line-height: 2.1rem;
  text-align: center;
  text-transform: none;
  color: #fff;
  margin: 0.8em 0;
}

.cta-join {
  font-size: 100%;
  font-weight: 400;
  font-family: inherit;
  font-style: normal;
  line-height: 1;
  text-align: left;
  text-decoration: none;
  background: #fff;
  box-shadow: 8px 8px 5px 0 rgba(85, 85, 85, 0.15);
  padding: 30px;
  margin: 0;
  border: 0;
}

.cta-join h3 {
  color: #007ce0;
  text-align: center;
  letter-spacing: -0.04em;
}

.cta-join a,
.cta-join a:hover {
  display: inline-block;
  width: 100%;
  font-size: 1.3rem;
  font-weight: bold;
  font-family: inherit;
  line-height: 1;
  text-align: center;
  text-decoration: none;
  text-shadow: none;
  text-transform: uppercase;
  color: #fff;
  background: #00b5f2;
  padding: 20px 10px;
  border-radius: 0;
}

.cta-join p {
  font-size: 0.9rem;
  font-weight: 300;
  letter-spacing: 0.02em;
  text-align: center;
  margin: 0.4rem 0;
}

/* Footer */
footer {
  text-align: center;
  margin-bottom: 40px;
}

footer .logo {
  width: 100%;
  max-width: 124px;
}

footer p {
  margin-bottom: 0.3em;
}

footer a {
  margin: auto 10px;
}

footer a:first-child {
  margin-left: 0;
}

footer .grey-text,
footer .grey-text a {
  color: #999;
}

footer .text-left {
  text-align: left;
}

footer .disclaimer {
  color: #3c444d;
  margin-top: 1em;
}

footer .disclaimer a {
  color: inherit;
}

.fa-social-grey {
  color: #999;
  text-align: right;
}

.fa-social-grey a,
.fa-social-grey a:hover {
  display: inline-block;
  position: relative;
  width: 2em;
  height: 2em;
  line-height: 2em;
  vertical-align: middle;
  text-decoration: none;
  color: inherit;
  margin: auto 5px;
}

.fa-social-grey a:last-child {
  margin-right: 0;
}

.fa-social-grey .full-size {
  height: 100%;
  width: 100%;
}
